<template>
  <div id="app" class="souye App">
    <!-- ============公共使用区域====================== -->
    <!-- 大图查看器 -->
    <el-dialog white="100%" :visible.sync="appimgflage" :before-close="appimgfalse">
      <a target="_blank" :href="appbigUrl | imgFilter" class="imgbtn cursorp" slot="title">查看原图</a>
      <img @click="setAppimgFlage([false])" id="imgBox" :src="appbigUrl | imgFilter" alt />
    </el-dialog>
    <!-- =================================== -->
    <div class="top">
      <i v-if="!isLogin" class="login el-icon-user cursorp" @click="loginboxopen"></i>
      <img
        v-if="isLogin"
        class="login cursorp"
        :src="loginUserData.head_img  | imgFilter | isNull"
        @click="headtoole"
        alt
      />
      <el-tooltip class="item" effect="dark" content="选择并输入关键字" placement="bottom">
        <i class="email">
          <el-input
            id="topsou"
            placeholder="请输入内容"
            size="mini"
            v-model="inputval"
            class="input-with-select"
            effect="dark"
            @change="tableData=[]"
          >
            <el-select slot="prepend" v-model="selectval" placeholder="请选择" style="width:85px">
              <el-option label="用户名" value="1"></el-option>
              <el-option label="手机号" value="2"></el-option>
            </el-select>
            <el-button @click="sousuoFn" slot="append" icon="el-icon-search" style="color:red"></el-button>
          </el-input>
        </i>
      </el-tooltip>
      <!-- 搜索框区域 -->
      <div class="sousuoalterbox" v-if="tableData.length != 0">
        <!-- <el-table :data="tableData" stripe style="width: 100%">
          <el-table-column prop="username" label="用户名"></el-table-column>
          <el-table-column prop="phone" label="手机号"></el-table-column>
        </el-table> -->
        <div v-for="(item,i) in tableData" :key="i" class="cursorp">
         <span>用户名：{{item.username}}</span>
         <span>手机号:{{item.phone | gphone}}</span>
         <span><a :href="location.replace(/\/.*/,'')+'?name='+item.username" target="_blank" rel="noopener noreferrer">前往个人主页</a></span>
        </div>
      </div>
    </div>
    <router-view v-if="aa" style="width:100%;height:100%;overflow: auto;"></router-view>
    <div class="bottom">
      <div>极简简历</div>
      <div>shuyuheng@aliyun.com</div>
    </div>
    <!-- 登录输入框 -->
    <el-dialog :title="logisreg?'登录':'注册'" :visible.sync="loginFlage" width="80%" id="loginDlog">
      <el-form ref="form">
        <el-form-item>
          <el-input placeholder="请输入手机号" v-model="username" prefix-icon="el-icon-user"></el-input>
        </el-form-item>
        <el-form-item>
          <el-input
            :type="pwdview ? 'text': 'password'"
            placeholder="请输入密码"
            v-model="password"
            prefix-icon="el-icon-cpu"
          >
            <i
              @click="pwdview = !pwdview"
              class="loginpassword"
              slot="suffix"
              :class="pwdview ? 'el-icon-unlock': 'el-icon-lock'"
            ></i>
          </el-input>
        </el-form-item>
        <el-form-item v-if="!logisreg">
          <el-input
            style="color:red"
            v-model="InvitationCode"
            placeholder="请输入邀请注册码"
            prefix-icon="el-icon-finished"
          ></el-input>
        </el-form-item>
      </el-form>
      <i class="bottom_title">{{logisreg ? '请登录您的博客' : '没有邀请码？请联系邮箱：2285128399@qq.com'}}</i>
      <span slot="footer" class="dialog-footer">
        <i @click="logisreg = true" v-if="!logisreg" class="logtext cursorp">已经账号前往登录?</i>
        <el-button @click="falseBtn">{{logisreg ? '注 册' : '取消'}}</el-button>
        <el-button type="primary" @click="trueBtn">{{logisreg ? '登 录' : '注册'}}</el-button>
      </span>
    </el-dialog>
    <nav>7878</nav>
  </div>
</template>
<script>
// 引入vuex
import { mapState, mapMutations, mapActions } from "vuex";
export default {
  computed: {
    ...mapState([
      "isLogin", //登录状态
      "appbigUrl", //查看大图图片路径
      "appimgflage", //是否打开查看大图
      "loadingflage", //是否打开加载动画
      "loginUserData" //用户所有数据
    ])
  },
  data() {
    return {
      location:location.href,
      aa: true,
      // 是否显示密码
      pwdview: false,
      selectval: "1",
      inputval: "",
      // 登录输入框
      loginFlage: false,
      logisreg: true,
      // 输入框对应数据
      username: "17768938296",
      password: "syh654321",
      InvitationCode: "",
      lodeingel: "",
      timeEl: "",
      tableData: []
    };
  },
  methods: {
    ...mapMutations([
      "setAppimgFlage", //修改图片查看器的开关 参数 [true/false,imgurl]
      "setLoadingFlage", //设置加载动画的开关 参数 true/false
      "setIsLogin" // 设置登录状态
    ]),
    ...mapActions([
      "getAllnavs", // 获取所有导航数据
      "getLoginUserData" //获取登录信息
    ]),
    // 搜索
    async sousuoFn() {
      let obj = {};
      if (this.selectval == 1) {
        obj.username = this.inputval;
      } else {
        obj.phone = this.inputval;
      }
      let { data: res } = await this.$http.get("/users", { params: obj });
      if(res.ok == 0) return this.$message({
          showClose: true,
          message: '请输入关键字...',
          type: 'error'
        });
        if(res.data.length == 0)return this.$message({
          showClose: true,
          message: '未查询到...',
          type: 'error'
        });
      this.tableData = res.data
    },
    headtoole() {
      this.$route.path == "/usercontrol"
        ? this.$router.push("/")
        : this.$router.push("/usercontrol");
      this.getLoginUserData();
    },
    appimgfalse() {
      this.setAppimgFlage([false]);
    },
    async trueBtn() {
      //判断是否是登录
      if (this.logisreg) {
        this.setLoadingFlage(true);
        let { data: res } = await this.$http.post("/users/login", {
          phone: this.username,
          password: this.password
        });
        this.setLoadingFlage(false);
        if (res.ok == 0)
          return this.$notify.error({
            title: res.error,
            customClass: "alter"
          });

        this.$notify.success({
          title: "登录成功~",
          customClass: "alter"
        });
        this.setIsLogin(true);
        // 保存token
        sessionStorage.setItem("token", res.token);
        // 获取登录信息
        this.getLoginUserData();
        // 获取导航数据
        this.getAllnavs();
        // 刷新页面
        this.aa = false;
        let that = this;
        setTimeout(() => {
          that.aa = true;
        }, 0);
        // // 关闭登录框
        this.loginFlage = false;
      } else {
        this.setLoadingFlage(true);
        // 请求接口
        this.$http
          .post("/users", {
            phone: this.username,
            password: this.password,
            auth_code: this.InvitationCode
          })
          .then(data => {
            this.setLoadingFlage(false);
            // 如果报错
            if (data.data.ok == 0)
              return this.$notify.error({
                title: data.data.error,
                customClass: "alter"
              });
            // 成功提示
            this.$notify.success({
              title: data.data.msg,
              customClass: "alter"
            });
            // 如果注册成功
            this.logisreg = true;
          });
      }
    },
    falseBtn() {
      // 判断是注册还是取消
      if (this.logisreg) {
        this.logisreg = false;
      } else {
        this.loginFlage = false;
      }
    },
    // 整页加载函数
    loading() {
      if (this.loadingflage) {
        this.lodeingel = this.$loading({
          // lock: true,
          text: "Loading",
          spinner: "el-icon-loading",
          background: "rgba(0, 0, 0, 0.7)"
        });
        this.timeEl = setTimeout(() => {
          // 判断
          this.$message.error("加载异常，请检查网络");
          this.lodeingel.close();
        }, 6000);
      } else {
        this.lodeingel.close();
        clearTimeout(this.timeEl);
      }
    },
    // 打开登录框
    loginboxopen() {
      // 判断路径是否位空
      if (/\?/g.test(this.$route.fullPath)) {
        let flage = confirm("当前路径不支持登录,是否跳转至可登录页面");
        if (flage) {
          location.href = location.href.replace(/\?.*/, "");
        }
        return;
      }
      // 打开登录框
      this.loginFlage = true;
    }
  },
  // 监听属性
  watch: {
    loadingflage: {
      handler() {
        this.loading();
      }
    }
  },
  filters: {
    isNull(val) {
      return val == null ? require("./assets/img/10k.jpg") : val;
    },
    gphone(val){
      // 17768938295
      return val.substr(0,3) + '****'+val.substr(7,4)
    }
  },
  created() {
    // 获取导航数据
    this.getAllnavs();
    if (this.$route.fullPath.indexOf("?") != -1) return;
    if (sessionStorage.getItem("token")) this.getLoginUserData();
  }
};
</script>
  
