import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: Home,
    children: [
      // 简介
      {
        path: '/synopsis',
        component: () => import('../views/Home/Synopsis')
      },
      // 剪影
      {
        path: '/cucoloris',
        component: () => import('../views/Home/Cucoloris')
      },
      // 伴侣
      {
        path: '/partner',
        component: () => import('../views/Home/Partner')
      },
      {
        path: '/blogs',
        component: () => import('../views/Home/blogs')
      },
      {
        path: '/travel',
        component: () => import('../views/Home/travel')
      },
      {
        path: '/leavemessage',
        component: () => import('../views/Home/leavemessage')
      },
      {
        path: '/blogx/:id',
        component: () => import('../views/Home/blogx')
      },
    ]
  },
  {
    path: '/not_found',
    component: () => import('../views/not_found')
  },
  {
    path: '/usercontrol',
    component: () => import('../views/Usercontrol.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  let flage = true
  function pathjc(routes) {
    for (let i = 0; i < routes.length; i++) {
      let item = routes[i]
      if (to.path.replace(/\/\d+/, '') == item.path.replace(/\/:.+/, '')) {
        return flage = false
      } else if (item.children) {
        pathjc(item.children)
      }
    }
  }
  pathjc(routes)

  if (flage) return next('/not_found')
  next()
})
export default router
