<template>
  <div id="app" class="Home">
    <v-touch
      v-on:swipeleft="swiperleft"
      v-on:swiperight="swiperright"
      class="wrapper"
      style="height:100%"
    >
      <div class="center">
        <router-view @back="swiperright" id="router-view" :class="{'is_path':is_path}"></router-view>
        <div class="content">
          <div
            :class="{'move_left':nav_val > index,'select':nav_val == index}"
            v-for="(item,index) in nav_data"
            :key="index"
            :style="item.img | bgimg"
          >
            <div
              style="width:100%;height:100%;opacity: 0.6;background-color:black;position: absolute;
      z-index: 6;"
            ></div>
            <div>
              <div class="juzhong">
                <p v-for="(item1,index1) in item.introduce" :key="index1">{{item1}}</p>
                <p class="btn cursorp" @click="$router.push(item.path)">查看详情</p>
              </div>
            </div>
            <div v-if="item.minimg" class="minimg">
              <div class="juzhong">
                <p>
                  <img
                    @click="setAppimgFlage([true,item.minimg])"
                    :src="item.minimg | imgFilter"
                    style="position: relative; z-index:10"
                    alt="博主图片"
                    class="cursorp"
                  />
                </p>
              </div>
            </div>
          </div>
        </div>
        <!-- 底部导航 -->
        <div class="nav-bottom">
          <div class="nav">
            <span class="yun" :style="`left:${nav_val * nav_width}px;width:${nav_width}px`"></span>
            <div
              ref="navs"
              :class="{'active':nav_val == index}"
              v-for="(item,index) in nav_data"
              :key="index"
              @click="nav_val=index"
              class="cursorp"
            >
              <i :class="item.ico + ' nav_ico'"></i>
              <div class="nav_text">{{item.text}}</div>
            </div>
          </div>
        </div>
      </div>
      <a href></a>
    </v-touch>
  </div>
</template>
<script>
import { mapState, mapMutations, mapActions } from "vuex";
export default {
  computed: {
    ...mapState([
      "allnavs", //导航数据
      "isLogin", //登录状态
    ])
  },
  data() {
    return {
      nav_val: 0,
      nav_width: 95,
      nav_data: [],
      // 路径布尔值
      is_path: false,
      nav_data_template: [
        {
          ico: "el-icon-s-custom",
          text: "简介",
          img: require("../assets/img/xiaogou.jpg"),
          introduce: [
            "舒榆衡",
            "重庆潼南 18 阳光活泼 i性格开朗",
            "喜欢我家的傻猪头"
          ],
          path: "/synopsis",
        },
        {
          ico: "el-icon-tickets",
          text: "文章",
          img: require("../assets/img/hua.jpg"),
          introduce: ["舒榆衡", "重庆潼南", "喜欢我家的傻猪头"],
          path:"/blogs"
        },
        {
          ico: "el-icon-picture",
          text: "简影",
          img: require("../assets/img/che.jpg"),
          introduce: ["舒榆衡", "重庆潼南", "喜欢我家的傻猪头"],
          path: "/cucoloris"
        },
        {
          ico: "el-icon-star-off",
          text: "伴侣",
          img: require("../assets/img/aiqing.jpg"),
          introduce: ["舒榆衡", "重庆潼南", "喜欢我家的傻猪头"],
          path: "/partner"
        },
        {
          ico: "el-icon-bicycle",
          text: "旅行",
          img: require("../assets/img/fengjing.jpg"),
          introduce: ["舒榆衡", "重庆潼南", "喜欢我家的傻猪头"],
          path:"/travel"
        },
        {
          ico: "el-icon-chat-dot-round",
          text: "留言",
          img: require("../assets/img/gudu.jpg"),
          introduce: ["舒榆衡", "重庆潼南", "喜欢我家的傻猪头"],
          path:"/leavemessage"
        }
      ]
    };
  },
  methods: {
    ...mapMutations([
      "setAppimgFlage", //修改图片查看器的开关 参数 [true/false,imgurl]
      "setLoadingFlage"
    ]),
    ...mapActions(["getAllnavs", "getRuserData"]),
    // 左滑右滑
    swiperleft: function() {
      if (this.$route.path != "/") return;
      if (this.nav_val >= this.nav_data.length - 1) {
        return (this.nav_val = 0);
      }
      this.nav_val += 1;
    },
    swiperright: function() {
      // 检查path是否变化
      // 判断 如果不是在 / 路径下
      if (this.$route.path != "/") {
        this.is_path = true;
        setTimeout(() => {
          this.is_path = false;
          this.$router.go("-1");
        }, 350);
        return;
      }
      if (this.nav_val <= 0) {
        return (this.nav_val = this.nav_data.length - 1);
      }
      this.nav_val -= 1;
    },
    // 获取nav数据结构
    getNav() {
      // 先清空
      this.nav_data = []
      let navs = this.allnavs.navs.split(",");
      // 执行一个6次的循环
      for (let i = 0; i < 6; i++) {
        if (navs[i] != 1) continue;
        // 判断第一个选项是否打开
        let item = this.nav_data_template[i];
        // 判断有没有图片
        if (this.allnavs[`n${i + 1}_img`] != null)
          item.minimg = this.allnavs[`n${i + 1}_img`];
        if (this.allnavs[`n${i + 1}_img`] == null) delete item.minimg;
        item.introduce = this.allnavs[`n${i + 1}`].split(",");
        this.nav_data.push(item);
      }
    }
  },
  // 监听
  watch: {
    allnavs: {
      handler(newarr) {
        // 判断
        if (newarr.length == 0) return;
        this.getNav();
      },
      deep: true,
      immediate: true
    }
  },
  created() {
    // 检测窗口变化
    window.onresize = () => {
      this.nav_width = this.$refs.navs[0].clientWidth;
    };
    // 判断有没有额外配置
    let myQuery = this.$route.query;
    if (this.$route.fullPath.indexOf('?') != -1) {
      // 清除所有登录状态
      sessionStorage.clear();
      myQuery.fn = () => {
        this.nav_data = this.nav_data_template;
        setTimeout(() => {
          alert("访问用户不存在~");
          location.href = location.href.replace(/\?.*/, "");
        }, 500);
      };
      this.getRuserData(myQuery);
    }
    // 判断有没有token
    if (!this.isLogin && this.$route.fullPath.indexOf('?') == -1) {
      return (this.nav_data = this.nav_data_template);
    }
  },
  filters: {
    bgimg(url) {
      return `background-image: url(${url});`;
    }
  },
  //
  mounted() {
    if (!this.isLogin&& this.$route.fullPath.indexOf('?') == -1) {
      this.nav_width = this.$refs.navs[0].clientWidth;
    }
    // 判断有没有token
    if (this.allnavs.length != 0) {
      this.nav_width = this.$refs.navs[0].clientWidth;
    }
  },
  updated() {
    // 值跟新
    this.nav_width = this.$refs.navs[0].clientWidth;
  }
};
</script>
